<template>
  <v-data-table
    :headers="headers"
    :items="companyInfo"
    sort-by="razaoSocial"
    class="elevation-1"
  >
    <template v-slot:top>
      <v-toolbar flat>
        <v-toolbar-title>Pedidos</v-toolbar-title>

        <v-spacer></v-spacer>

        <v-dialog
          v-model="dialog"
          max-width="700px"
          shaped
        >
        
        <template v-slot:activator>
          <router-link to="/novo-pedido-1" class="text-decoration-none">
            <v-btn
              color="primary"
              dark
              class="mb-2"
            >
              Novo Pedido
            </v-btn>
          </router-link>
        </template>

          <!-- Company Register Modal Start -->
          <v-card shaped>
            <v-card-title>{{ formTitle }}</v-card-title>
            
            <v-card-text lazy-validation>  
              <v-text-field
                v-model="editedItem.cnpj"
                v-mask="'##.###.###/####-##'"
                :value="currentValue"
                @input="handleInput"
                label="CNPJ"
                prepend-icon="mdi-card-text"
                :rules="required"
                required
              />
              <v-text-field
                v-model="editedItem.razaoSocial"
                label="Razão Social"
                prepend-icon="mdi-clipboard-account"
                :rules="required"
                required
              />
              <v-text-field
                v-model="editedItem.nomeFantasia"
                label="Nome Fantasia"
                prepend-icon="mdi-clipboard-account"
              />
              <v-row class="mx-1">
                <v-select
                  v-model="editedItem.crt"
                  :items="crt"
                  label="CRT"
                />
                <v-text-field
                v-model="editedItem.phoneNumber"
                label="Número de Celular"
                v-mask="'(##) # ####-####'"
                prepend-icon="mdi-cellphone"
                />
              </v-row>                
              <v-row class="mx-1">
                <v-text-field
                  v-model="editedItem.cep"
                  label="CEP"
                  v-mask="'##.###-###'"
                  prepend-icon="mdi-office-building-marker"
                  :rules="required"
                />
                <v-text-field
                  v-model="editedItem.addressNumber"
                  label="Número"
                  prepend-icon="mdi-numeric"
                  :rules="required"
                />
              </v-row>
              <v-text-field
                v-model="editedItem.complemento"
                label="Complemento"
                :hint="'opcional'"
                prepend-icon="mdi-factory"
              />
              <v-row class="mx-1">
                <v-select
                  v-model="editedItem.uf"
                  :items="uf"
                  label="UF"
                  v-mask="'AA'"
                  class="mr-1"
                />
                <v-text-field
                  v-model="editedItem.city"
                  label="Cidade"
                />
              </v-row>
            </v-card-text>
            
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn
                color="blue darken-1"
                text
                @click="close"
              >
                Cancelar
              </v-btn>
              <v-btn
                color="blue darken-1"
                text
                @click="save"
              >
              Cadastrar
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
        <!-- Company Register Modal End -->

        <!-- Delete Modal -->
        <v-dialog v-model="dialogDelete" max-width="700px">
          <v-card>
            <v-card-title class="text-h6">Desativar Empresa</v-card-title>
            <v-card-text class="red--text">Tem certeza que deseja desativar essa empresa?</v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="closeDisable">Cancelar</v-btn>
              <v-btn color="blue darken-1" text @click="disableCompanyConfirm">Desativar</v-btn>
              <v-spacer></v-spacer>
            </v-card-actions>
          </v-card>
        </v-dialog>
        <!-- Delete Modal End -->

      </v-toolbar>
    </template>
    
    <template v-slot:[`item.actions`]="{ item }">
      <v-icon
        small
        class="mr-2"
        @click="editItem(item)"
      >
        mdi-pencil
      </v-icon>
      <v-icon
        small
        class="mr-2"
        @click="disableCompany(item)"
      >
        mdi-delete
      </v-icon>
    </template>

    <template v-slot:no-data>
      <v-btn
        color="primary"
        @click="initialize"
      >
        Reset
      </v-btn>
    </template>
  </v-data-table>
</template>

<!-- 
  Situações:
  • em análise do órgão
  • aguardando pagamento
  • capa liberada
  • licenca liberada
  • erro de cadastro no órgão
  • aguardando play
  • arquivada
 -->

<!-- DIVISOR DIVISOR DIVISOR DIVISOR DIVISOR DIVISOR DIVISOR DIVISOR DIVISOR DIVISOR DIVISOR DIVISOR -->

<script>
  export default {
    name: 'CompanyList',
    data: () => ({
      showPassword: false,
      showPassword2: false,
      dialog: false,
      dialogDelete: false,
      menu: false,
      search: '',
      required: [
        v => !!v || 'Este campo é obrigatório',
      ],
      crt: ['Simples Nacional', 'Lucro Real', 'Lucro Presumido'],
      uf: ['AC', 'AL', 'AM', 'AP', 'BA', 'CE', 'DF', 'ES', 'GO', 'MA', 'MG', 'MS', 'MT', 'PA', 'PB', 'PE', 'PI', 'PR', 'RJ', 'RN', 'RO', 'RR', 'RS', 'SC', 'SE', 'SP', 'TO'],
      headers: [
        {
          text: 'Empresa',
          align: 'start',
          sortable: true,
          value: 'razaoSocial',
        },
        { text: 'CNPJ', value: 'cnpj' },
        { text: 'UF', value: 'uf' },
        { text: '', value: 'actions', sortable: false },
      ],
      companyInfo: [],
      editedIndex: -1,
      editedItem: {
        razaoSocial: '',
        cnpj: '',
        registerDate: '',
        uf: ''
      },
      defaultItem: {
        razaoSocial: '',
        cnpj: '',
        registerDate: '',
        uf: ''
      },
    }),

    computed: {
      formTitle () {
        return this.editedIndex === -1 ? 'Cadastrar Empresa' : 'Editar Empresa'
      },
    },

    watch: {
      dialog (val) {
        val || this.close()
      },
      dialogDelete (val) {
        val || this.closeDisable()
      },
    },

    created () {
      this.initialize()
    },

    methods: {
      initialize () {
        this.companyInfo = [
          {
            razaoSocial: 'P S DE ALMEIDA',
            cnpj: '80.309.122/0001-01',
            registerDate: '12/05/2019',
            uf: "SP",
          },
        ]
      },

      editItem (item) {
        this.editedIndex = this.companyInfo.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },

      disableCompany (item) {
        this.editedIndex = this.companyInfo.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialogDelete = true
      },

      disableCompanyConfirm () {
        this.companyInfo.splice(this.editedIndex, 1)
        this.closeDisable()
      },

      close () {
        this.dialog = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      closeDisable () {
        this.dialogDelete = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      save () {
        if (this.editedIndex > -1) {
          Object.assign(this.companyInfo[this.editedIndex], this.editedItem)
        } else {
          this.companyInfo.push(this.editedItem)
        }
        this.close()
      },
    },
  }
</script>